body {
  background-color: #000000;
  background-image: url('//source.unsplash.com/random/1280x1024');
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center; 
  background-size: cover;
  font-family: 'Saira Semi Condensed', sans-serif;
}

.root {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
}

Clock {
  text-align: center;
}

.Grettings {
  text-align: right;
  color: white;
  text-shadow: 2px 2px #000000;
  margin: 0 10px;
}

.Clock {
  color: white;
  text-shadow: 2px 2px #000000;
  font-size: 48px;
  text-align: center;
}

.Code {
  padding: 8px;
  background-color: rgba(255, 255, 255, 0.6);  
  border-radius: 5px;
  border:1px;
  text-align: right;
  max-width: 70%;
  margin-left: 15%;
}

.Code .advice {
  text-align: center;
}

.icon {
  float:left;
}

.iconImg {
  /* background-image: url('//lorempixel.com/60/60/'); */
  border-radius:      4em;
  height:             60px;
  -moz-border-radius: 4em;
  width:              60px;
}

.Code span {
  margin: 5px;
}

.Code span:hover {
  cursor: pointer;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.ReferenceList {
  position: fixed;
  bottom: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-color: rgba(255, 255, 255, 0.6);
  max-width: 80%;
  margin-left:10%;
}

.ReferenceItem {
  color: #000000;
  text-decoration: none;
  margin: 0 5px 0 5px;
}


